.App {
    max-width: 1230px;
    margin: 0 auto;
    padding: 0 15px;
}
.header {
    background-color: #423f46;
}
.navbar {
    max-width: 1230px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    column-gap: 20px;
    padding: 20px;
    height: 50px;
    a {
        color: #faed26;
        font-family: Stick;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}
